<template>
  <div>
    <VeLine :data="data" :settings="settings" :grid="grid"></VeLine>
  </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'
export default {
  props: ['data', 'settings', 'grid'],
  components:{
    VeLine
  }

}
</script>



